@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap");

:root {
  --body-bg: #292929;
  --content-bg: #1d1d1d;
  --text-color: #e5e5e5;
}

body {
  margin: 0;
  font-size: 1rem;
  font-family: "Roboto Mono", monospace;
  background: var(--body-bg);
  color: var(--text-color);
}

/* Global */
svg {
  width: 1.5rem;
}

button.icon {
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
}

.container {
  max-width: 50rem;
  margin: 0 auto;
  padding: 0 0.5rem 0 0.5;
  display: flex;
  align-items: center;
  height: 100%;
}

/* Header */
header.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 4.5rem;
  background: var(--content-bg);
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.3);
  z-index: 10;
}

header .logo-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

header .logo {
  display: flex;
  align-items: center;
  cursor: default;
}

header .logo span {
  margin-left: 0.5rem;
}

/* Post */
.article {
  margin: 4rem 0;
}

.post-wrapper {
  width: 100%;
}

/* Code */
.code {
  position: relative;
}

.code .copy-icon {
  position: absolute;
  top: 1rem;
  right: 1rem;
  z-index: 5;
}
